.flexContainer {
  width: 100%;
  height: 100vh;
}

.flexContainer > * {
  height: 100%;
}

.rowParent, .columnParent {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-content: stretch;
  align-items: stretch;
}

.columnParent{
  flex-direction: column;
}

.flexChild {
  flex: 1;
  align-self: auto;
}

.buttonsPanel {
  button {
    margin-bottom: 10px;
  }

  & > button {
    margin-right: 20px;
  }
}

// Default styling for all buttons
button {
  transition: background-color 100ms ease-in-out;
  display: inline-block;
  padding: 0 20px;
  text-transform: uppercase;
  font-size: 0.75rem;
  line-height: 36px;
  font-weight: 700;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  outline: none;
  border: solid rgb(105, 92, 92);
  color: black;
  background-color: transparent;
}